.page.page-loading{
  width:100%;
  height: 100%;
  background: url('../images/loading.jpg') center center no-repeat;

}

.page.page-loading .loading-box {
  display: block;
}

.page.page-loading .loading-box {
	position: absolute;
	left: 0;
	// top: 50%;
	width: 100%;
	margin-top: 300px
}

.page.page-loading .loading-box .loading-animate {
	background: url(../images/loading_t1.png) center center repeat;
	width: 412px;
	height: 137px;
	margin: 0 auto 30px;
	position: relative
}

.page.page-loading .loading-box .loading-animate .rotation {
	// background: url(http://n.sinaimg.cn/gd/20180507/nfdw/home_right_bottom_light_rotation.png) center 0 no-repeat;
	background-size: cover;
	position: absolute;
	left: 36px;
	top: 57px;
	width: 107px;
	height: 107px;
	-webkit-animation: Rotate360 2s linear infinite;
	animation: Rotate360 2s linear infinite
}

.page.page-loading .loading-box .progress-num {
	width: 100%;
	height: 30px;
  margin-top: 450px;
}

.page.page-loading .loading-box .progress-num p {
	color: black;
	text-align: center;
	font-size: 28px
}

